declare global {
    interface Window {
        _env_: {
            REACT_APP_API_URL: string;
            REACT_APP_PUBLIC_URL: string;
            REACT_APP_REDASH_PUBLIC_URL: string;
            REACT_APP_REDASH_COMMENTS_PUBLIC_URL: string;

            // Formio
            REACT_APP_API_PROJECT_URL: string;
            REACT_APP_FORM_ID: string;
            REACT_APP_FORMIO_JWT_SECRET: string;
            REACT_APP_USER_RESOURCE_FORM_ID: string;
            REACT_APP_FORMIO_ANONYMOUS_USER: string;
            REACT_APP_ANONYMOUS_ID: string;

            // Keycloak
            REACT_APP_KEYCLOAK_URL: string;
            REACT_APP_KEYCLOAK_CLIENT: string;
            REACT_APP_KEYCLOAK_REALM: string;
            REACT_APP_KEYCLOAK_ADMIN_ROLE: string;
        };
    }
}

const env = () => window._env_ || process.env || {};

// adding localStorage to access the MET API from external sources(eg: web-components)
const API_URL = localStorage.getItem('met-api-url') || env()['REACT_APP_API_URL'];
const PUBLIC_URL = localStorage.getItem('met-public-url') || env()['REACT_APP_PUBLIC_URL'];
const REDASH_DASHBOARD_URL = env()['REACT_APP_REDASH_PUBLIC_URL'];
const REDASH_CMNTS_DASHBOARD_URL = env()['REACT_APP_REDASH_COMMENTS_PUBLIC_URL'];

// Formio Environment Variables
const FORMIO_PROJECT_URL = env()['REACT_APP_API_PROJECT_URL'];
const FORMIO_API_URL = env()['REACT_APP_API_PROJECT_URL'];
const FORMIO_FORM_ID = env()['REACT_APP_FORM_ID'];
const FORMIO_JWT_SECRET = env()['REACT_APP_FORMIO_JWT_SECRET'];
const FORMIO_USER_RESOURCE_FORM_ID = env()['REACT_APP_USER_RESOURCE_FORM_ID'];
const FORMIO_ANONYMOUS_USER = env()['REACT_APP_FORMIO_ANONYMOUS_USER'];
const FORMIO_ANONYMOUS_ID = env()['REACT_APP_ANONYMOUS_ID'];

// Keycloak Environment Variables
const KC_URL = env()['REACT_APP_KEYCLOAK_URL'];
const KC_CLIENT = env()['REACT_APP_KEYCLOAK_CLIENT'];
const KC_REALM = env()['REACT_APP_KEYCLOAK_REALM'];
const KC_ADMIN_ROLE = env()['REACT_APP_KEYCLOAK_ADMIN_ROLE'];

export const AppConfig = {
    apiUrl: API_URL,
    publicUrl: PUBLIC_URL,
    redashDashboardUrl: REDASH_DASHBOARD_URL,
    redashCmntsDashboardUrl: REDASH_CMNTS_DASHBOARD_URL,
    formio: {
        projectUrl: FORMIO_PROJECT_URL,
        apiUrl: FORMIO_API_URL,
        formId: FORMIO_FORM_ID,
        anonymousId: FORMIO_ANONYMOUS_ID || '',
        anonymousUser: FORMIO_ANONYMOUS_USER || 'anonymous',
        userResourceFormId: FORMIO_USER_RESOURCE_FORM_ID,
        // TODO: potentially sensitive information, should be stored somewhere else?
        jwtSecret: FORMIO_JWT_SECRET || '',
    },
    keycloak: {
        url: KC_URL || '',
        clientId: KC_CLIENT || '',
        realm: KC_REALM || '',
        adminRole: KC_ADMIN_ROLE || 'admin',
    },
};
